import {
    Paper,
    Typography,
    Grow,
    Card,
    CardMedia,
    CardContent,
    makeStyles,
    Grid,
    Zoom
} from '@material-ui/core';

import React from 'react';
import colores from '../styles/colors';
import { isMobile } from 'react-device-detect';
import jorAmbiente from '../assets/jor/jorAmbiente.jpg';

const EjesContent = ({ data, children }) => {
    const classes = useStyles(data);

    return (
        <div>
            <Grid container spacing={3}>
                <Grid item xs={12} sm={6}>
                    <Zoom in={true} timeout={400}>
                        <Card
                            elevation={5}
                            style={{
                                height: isMobile ? 120 : '100%',
                                borderRadius: 15
                            }}
                        >
                            <CardMedia
                                image={data.img}
                                style={{
                                    height: '100%'
                                }}
                            />
                        </Card>
                    </Zoom>
                </Grid>
                <Grid item sm={6}>
                    <Paper elevation={5} className={classes.paperText}>
                        <Zoom in={true} timeout={400}>
                            <Typography className={classes.letter}>
                                {data.text}
                            </Typography>
                        </Zoom>
                    </Paper>
                </Grid>
                <Grid item sm={4}>
                    <Zoom in={true} timeout={400}>
                        <Paper elevation={5} className={classes.paperCards}>
                            <Typography className={classes.letter}>
                                {data.card0.txtT}
                            </Typography>
                            <Typography className={classes.textCard}>
                                {data.card0.txtA}
                            </Typography>
                            <Typography className={classes.textCard}>
                                {data.card0.txtB}
                            </Typography>
                            <Typography className={classes.textCard}>
                                {data.card0.txtC}
                            </Typography>
                            <Typography className={classes.textCard}>
                                {data.card0.txtD}
                            </Typography>
                        </Paper>
                    </Zoom>
                </Grid>
                <Grid item sm={4}>
                    <Zoom in={true} timeout={400}>
                        <Paper elevation={5} className={classes.paperCards}>
                            <Typography className={classes.letter}>
                                {data.card1.txtT}
                            </Typography>
                            <Typography className={classes.textCard}>
                                {data.card1.txtA}
                            </Typography>
                            <Typography className={classes.textCard}>
                                {data.card1.txtB}
                            </Typography>
                        </Paper>
                    </Zoom>
                </Grid>
                <Grid item sm={4}>
                    <Zoom in={true} timeout={400}>
                        <Card elevation={5} className={classes.cMedia}>
                            <CardMedia
                                image={data.card2.img}
                                style={{ height: '100%' }}
                            />
                        </Card>
                    </Zoom>
                </Grid>
            </Grid>
        </div>
    );
};

const useStyles = makeStyles((data) => ({
    paperText: {
        padding: isMobile ? 15 : 25,

        borderRadius: 15,
        height: isMobile ? 170 : 280,
        overflow: 'auto',
        backgroundColor: (data) => data.color
    },
    letter: {
        letterSpacing: 1.2,
        fontSize: isMobile ? 12 : 16,
        color: '#000'
    },
    paperCards: {
        padding: 25,
        borderRadius: 15,
        height: 200,
        overflow: 'auto',
        backgroundColor: (data) => data.color
    },
    textCard: {
        textAlign: 'left',
        marginTop: 3,
        fontSize: isMobile ? 12 : 14,
        overflowY: 'scroll',
        boxSizing: 'border-box'
    },
    cMedia: {
        height: 200,
        borderRadius: 15
    },
    '@global': {
        '*::-webkit-scrollbar': {
            width: '0em'
        }
    }
}));

export default EjesContent;
