import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Avatar from '@material-ui/core/Tab';
import PhoneIcon from '@material-ui/icons/Phone';
import FavoriteIcon from '@material-ui/icons/Favorite';
import PersonPinIcon from '@material-ui/icons/PersonPin';
import HelpIcon from '@material-ui/icons/Help';
import ShoppingBasket from '@material-ui/icons/ShoppingBasket';
import ThumbDown from '@material-ui/icons/ThumbDown';
import ThumbUp from '@material-ui/icons/ThumbUp';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import colores from '../styles/colors';
import { Fab } from '@material-ui/core';

import Beg from '../components/provContent/beg2';
import Billetera from '../components/provContent/billetera';
import Vacunas from '../components/provContent/vacunas';
import Incluir from '../components/provContent/incluir';
import mod from '../assets/ejesProv/begicon.jpg';
import begIcon from '../assets/ejesProv/begicon.jpg';
import billetera from '../assets/ejesProv/bille.png';
import vacuna from '../assets/ejesProv/vacSF.png';
import incluir from '../assets/ejesProv/incluir.jpg';
import { AppContext } from '../context/appContext';
import SportsSoccerIcon from '@material-ui/icons/SportsSoccer';
import { IconTab } from '../info/provincia';
import noi from '../assets/noimg.jpg';

export default function TabsProv({ value, setValue }) {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <Tabs
                value={value}
                onChange={(e, val) => setValue(val)}
                variant="fullWidth"
                scrollButtons="off"
                indicatorColor="primary"
                textColor="primary"
                aria-label="tabs Prov"
                style={{
                    paddingTop: 10,
                    backgroundColor: colores.mainLightfull
                }}
            >
                {IconTab.map((btn) => {
                    return <Tab icon={btn.ico} />;
                })}
            </Tabs>
        </div>
    );
}

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: theme.palette.background.paper
    }
}));
